body {
  margin: 0;
}

.Draggable {
  border-radius: 3px;
  box-shadow: 2px 3px 11px 2px rgba(0,0,0,0.2);
  display: flex;
  justify-content: space-between;
  margin: 10px;
  cursor: move;
}

.Droppable {
  grid-column: span 1;
  border: solid black 2px;
  padding: 20px;
  margin: 2px;
  border-radius: 20px;
}

.droppable-top {
  display: flex;
  justify-content: space-between;
}

.App {
  font-family: Georgia;
}

.delete-button,input {
  height: -webkit-min-content;
  height: -moz-min-content;
  height: min-content;
}

.the-lists {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
}

form {
  margin: 20px;
  margin-bottom: 0;
  display: flex;
  justify-content: stretch;
}

form button {
  font-size: 13px;
}

.droppable-top h1 {
  margin: 0;
}
